<template>
  <div class="login-box">
    <van-form @submit="submitLogin">
      <van-field
          v-model="username"
          name="用户名"
          label="用户名"
          placeholder="用户名"
          autocomplete="off"
          :rules="[{ required: true, message: '请填写用户名' }]"
      />
      <van-field
          v-model="password"
          type="password"
          name="密码"
          label="密码"
          placeholder="密码"
          :rules="[{ required: true, message: '请填写密码' }]"
      />
      <div style="margin: 16px;">
        <van-button round block
                    type="info"
                    native-type="submit"
                    :loading="loading"
                    loading-text="登录中..."
        >
          登录
        </van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
import {reqLogin} from '@/api'

export default {
  name: 'Login',
  data() {
    return {
      username: '',
      password: '',
      loading: false,
    }
  },
  methods: {
    submitLogin() {
      this.loading = true
      reqLogin({
        userName: this.username,
        password: this.password,
      }).then(res => {
        if (res.code === 'success') {
          this.$toast('用户登录成功，稍后跳转至首页')
          localStorage.setItem('token', res.token)
          localStorage.setItem('username', this.username)
          this.$router.replace('/')
        } else {
          this.$toast('账号或密码错误')
          this.username = this.password = ''
          this.loading = false
        }
      }).catch(err => {
        this.$toast.fail('未知错误，请稍后重试')
        this.loading = false
      })
    }
  }
}
</script>

<style scoped>
.login-box {
  margin: 30px auto;
}
</style>
